.overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgba(0, 0, 0, 0.8);
    z-index: 1200;
    cursor: auto;
}

.modal {
    max-width: calc(100vw - 48px);
    max-height: calc(100vh - 24px);
    background-color: #fff;
    padding: 15px;
    &__header {
        display: flex;
        align-items: center;
        justify-content: space-between;
        &_title {
            font-size: 25px;
        }
    }
    &__dates {
        display: flex;
        justify-content: space-between;
        margin-top: 25px;
        margin-bottom: 25px;
    }
    &__buttons {
        display: flex;
        justify-content: flex-end;
    }
}
